<template>
<!--报修详情-->
<div class="repairDetail">
	<!--报修信息的头部-->
	<header class="clear" v-if="repairState">
		<div class="fl">
			<i :class="repairState.icon"></i>
			<span>{{repairState.cont}}</span>
		</div>
		<div v-if="repairState.position==3" class="fl" @touchstart.prevent="toEvaluate">
			<span>
				去评价
			</span>
		</div>
	</header>
	<section>
		<h4>报修详情</h4>
		<div class="repairDetailInfo">
			<ul class="maintainMesList">
				<li>
					<span>报修建筑</span>
					<span>{{repairBuilding}}</span>
				</li>
				<li>
					<span>报修地点</span>
					<span>{{repairPlace}}</span>
				</li>
				<li>
					<span>报修问题</span>
					<span>{{repairIssue}}</span>
				</li>
				<li>
					<span>报修时间</span>
					<span>{{repairTime}}</span>
				</li>
				<li>
					<span>联系方式</span>
					<span>{{repairPhone}}</span>
				</li>
				<li>
					<span>添加备注</span>
					<span>{{repairRemark}}</span>
				</li>
			</ul>
			<div class="repairImgs clear">
				<img v-if="repairPicArr" v-for="item in repairPicArr" :src="item" @touchstart.prevent="previewTheImage(item)" />
			</div>
		</div>
	</section>
	<footer v-if="principal">
		<h4>维修信息</h4>
		<ul class="maintainMes" v-if="principal">
			<!--维修信息-->
			<li class="clear">
				<div>
					<i class="icon iconfont icon-dianligongchengshi"></i>
					<span>维修人</span>
				</div>
				<div class="name">
					<span @touchstart.prevent="toPersonInfo(principal.workerId)">
						<!--负责人-->
						{{principal.workerName}}(负责人)
					</span>
					<span v-if="general&&general.length" v-for="item in general" @touchstart.prevent="toPersonInfo(general.workerId)">
						{{item.workerName}}
					</span>
				</div>
			</li>
			<li v-if="startTime">
				<div>
					<i class="icon iconfont icon-kaishishijian"></i>
					<span>开始时间</span>
				</div>
				<div>
					{{startTime}}
				</div>
			</li>
			<li v-if="endTime">
				<div>
					<i class="icon iconfont icon-zhi"></i>
					<span>结束时间</span>
				</div>
				<div>
					{{endTime}}
				</div>
			</li>
		</ul>
	</footer>
</div>
</template>
<script type="ecmascript-6">
import {
	mapGetters
} from 'vuex'
import {
	previewImage
} from 'api/global'
import {
	getRepairDetail
} from 'api/repair'
import tools from 'utils/tools'
export default {
	data() {
		return {
			id: 0, //工单的id
			repairBuilding: '', //报修的建筑
			repairPlace: '', //报修的地点
			repairIssue: '', //报修问题
			repairTime: 0, //报修时间
			repairPhone: 0, //报修联系电话
			repairState: null, //工单状态
			repairRemark: '', //备注信息
			repairPicArr: [], //图片列表
			principal: null, //负责人（对象）
			general: null, //非负责人（数组）
			startTime: 0, //起始时间
			endTime: 0, //结束时间
			repairStatus: [{
				position: 0,
				icon: 'icon iconfont icon-shoulizhong',
				cont: '工单受理中'
			}, {
				position: 1,
				icon: 'icon iconfont icon-ganwangxianchang',
				cont: '工程师正火速赶往现场'
			}, {
				position: 2,
				icon: 'icon iconfont icon-Wrench',
				cont: '正在努力维修中'
			}, {
				position: 3,
				icon: 'icon iconfont icon-issue',
				cont: '您还没有评价，请快去评价吧'
			}]
		}
	},
	computed: { ...mapGetters(['jobId'])
	},
	methods: {
		toEvaluate() {
			this.$router.push({
				name: 'evaluate',
				params: {
					id: this.id
				}
			});
		},
		toPersonInfo(id) {
			this.$router.push({
				name: 'personInfo',
				params: {
					id
				}
			});
		},
		previewTheImage(item) {
			previewImage(item, this.repairPicArr)
		}
	},
	created() {
		document.title = "报修详情"
		//通过id获取要渲染的信息
		this.id = this.$route.params.id || this.jobId;
		console.log(this.id);
		getRepairDetail(this.id)
			.then(res => {
				if (res.data.Result == "success") {
					const data = res.data.Content
					//保存状态信息
					switch (data.status) {
						case '受理中':
							this.repairState = this.repairStatus[0];
							break;
						case '工程师已前往报修地点':
							this.repairState = this.repairStatus[1];
							break;
						case '维修中':
							this.repairState = this.repairStatus[2];
							break;
						case '待评价':
							this.repairState = this.repairStatus[3];
							break;
					}
					//保存报修建筑
					this.repairBuilding = data.projectName;

					//问题：没有建筑地点
					this.repairPlace = data.place;

					//报修问题
					this.repairIssue = data.repairDetail;
					//报修时间
					this.repairTime = tools.getDates(data.time);
					//联系电话
					this.repairPhone = data.phone;
					if (Array.isArray(data.remark) && data.remark.length > 0) {
						for (let remarkItem of data.remark) {
							switch (remarkItem.name) {
								case "1":
									//备注信息
									this.repairRemark = remarkItem.value
									break
								case "2":
									this.repairPicArr.push(remarkItem.value)
									break
								default:
									break
							}
						}
					}
					//维修的开始时间
					this.startTime = tools.getDates(data.startTime);
					//维修的结束时间
					this.endTime = tools.getDates(data.endTime);
					//负责人
					this.principal = data.own;
					//非负责人
					this.general = data.workers.slice(1);
				}
			})
	}
}
</script>
<style scoped lang="less">
@rem: 30rem;
.repairDetail {
	width: 100%;
}

.repairDetail h4 {
	padding-top: 25/@rem;
	padding-left: 42/@rem;
	height: 84/@rem;
	line-height: 64/@rem;
	font-size: 24/@rem;
	font-weight: normal;
	color: #6d6d6d;
}

.repairDetail header {
	background-color: #fff;
	height: 111/@rem;
	line-height: 111/@rem;
	padding-left: 21/@rem;
	div:first-of-type {
		width: 549/@rem;
		i {
			display: inline-block;
			margin: 10/@rem 21/@rem;
			width: 50/@rem;
			height: 50/@rem;
			background-color: #e7ecf0;
			color: #000;
			line-height: 54/@rem;
			text-align: center;
			border-radius: 4px;
		}
		span {
			display: inline-block;
			font-size: 32/@rem;
			color: #1c1c20;
		}
	}
	div:nth-of-type(2) {
		height: 100%;
		display: flex;
		span {
			display: block;
			width: 150/@rem;
			margin: auto;
			height: 50/@rem;
			line-height: 50/@rem;
			color: #fff;
			background-color: #44b197;
			text-align: center;
			border-radius: 25/@rem;
		}
	}
}

.repairDetailInfo {
	width: 100%;
	background-color: #fff;
	.maintainMesList {
		padding: 20/@rem;
		li {
			span {
				font-size: 27/@rem;
				line-height: 60/@rem;
				display: inline-block;
			}
			span:first-of-type {
				padding: 0 32/@rem;
				height: 60/@rem;
				color: #9597a0;
				vertical-align: top;
			}
			span:nth-of-type(2) {
				max-width: 510/@rem;
			}
		}
	}
}

.repairImgs {
	padding-left: 30/@rem;
	padding-bottom: 30/@rem;
	img {
		float: left;
		margin-left: 10/@rem;
		width: 160/@rem;
		height: 160/@rem;
		border-radius: 4px;
	}
}

.maintainMes {
	background-color: #fff;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	padding: 20/@rem 0;
	li {
		div {
			float: left;
			line-height: 75/@rem;
			font-size: 27/@rem;
		}
		div:nth-of-type(1) {
			color: #9597a0;
			padding-left: 18/@rem;
			i {
				display: inline-block;
				margin: 0 18/@rem;
			}
		}
		div:nth-of-type(2) {
			max-width: 510/@rem;
			margin-left: 20/@rem;
		}
		div:nth-of-type(2).name {
			color: #44b197;
		}
	}
}
</style>


